.calendar {
  display: inline-block;
  padding: 30px; }
  .calendar .calendar-header .month-name {
    text-align: left;
    color: #3276b0;
    font-size: 18px;
    font-weight: bold; }
  .calendar .calendar-header .calendar-controls {
    float: right;
    position: relative;
    top: -20px; }
    .calendar .calendar-header .calendar-controls .date-selector {
      -webkit-appearance: none;
      border-radius: 0px;
      font-size: 12px;
      border: 1px solid #dcdcdc;
      background: #ffffff;
      padding: 6px 10px;
      color: #3276b0;
      font-weight: bold;
      margin-right: 8px;
      cursor: pointer;
      padding: 6.5px; }
    .calendar .calendar-header .calendar-controls .btn-month-nav {
      border: 1px solid #dcdcdc;
      background: #ffffff;
      padding: 6px 10px;
      color: #3276b0;
      font-weight: bold;
      background: #f9f9f9;
      padding: 6.5px;
      margin-right: 4px;
      cursor: pointer; }
      .calendar .calendar-header .calendar-controls .btn-month-nav svg.fa-icon {
        margin: 0px 5px; }
  .calendar .calendar-body {
    clear: both; }
    .calendar .calendar-body .days-header {
      border-bottom: 1px solid #eaeaea; }
      .calendar .calendar-body .days-header .day-type {
        display: inline-block;
        width: 110px;
        text-align: right;
        padding-right: 10px;
        padding-bottom: 6px;
        font-size: 12px;
        text-transform: uppercase;
        color: #3276b0;
        font-weight: bold; }
    .calendar .calendar-body .day-row .calendar-day {
      display: inline-block;
      border-left: 1px solid #eaeaea;
      border-bottom: 1px solid #eaeaea;
      height: 75px;
      width: 110px;
      padding-top: 10px;
      padding-right: 10px;
      font-size: 15px;
      text-align: right;
      color: #292c34; }
    .calendar .calendar-body .day-row .sunday {
      background: #f9f9f9;
      color: #292c34; }
    .calendar .calendar-body .day-row .overflow {
      color: #888888; }
    .calendar .calendar-body .day-row .current-date span {
      background: #3276b0;
      color: #ffffff;
      display: inline-block;
      text-align: center;
      padding: 4px 7px;
      border-radius: 3px;
      font-size: 12px;
      font-weight: 600; }

